import React, { useState } from 'react';
import { Input, Icon, Button } from "atomize";
import classes from './Login.module.css';


const Login = ({RouteChangers}) => {
    const [showPassword, showPW] = useState(false);

    return (
        <div className={classes.containerLogin}>
            <h1>Lets Sign In</h1>
            <h2>Welcome Back</h2>
            <div className={classes.LoginBox}>
                <div className={classes.Login}>
                    <Input
                        placeholder="User Name"
                        p={{ x: "2.5rem" }}
                        textWeight="100"
                        prefix={
                            <Icon
                                name="UserSolid"
                                color="warning800"
                                size="16px"
                                cursor="pointer"
                                pos="absolute"
                                top="50%"
                                left="0.75rem"
                                transform="translateY(-50%)"
                            />
                        }
                    />
                </div>
                <div className={classes.Login}>
                    <Input
                        placeholder="Password"
                        type={showPassword ? "text" : "password"}
                        suffix={
                            <Button
                                pos="absolute"
                                onClick={() => showPW(!showPassword)}
                                bg="transparent"
                                w="3rem"
                                right="0"
                                cursor="pointer"
                                top="50%"
                                transform="translateY(-50%)"
                                rounded={{ r: "md" }}
                            >
                                <Icon
                                    name={showPassword ? "EyeSolid" : "Eye"}
                                    color={showPassword ? "danger800" : "success800"}
                                    size="16px"
                                />
                            </Button>
                        }
                    />
                </div>
                <div className={classes.LoginBtn}>
                    <Button
                        suffix={
                            <Icon
                                name="LongRight"
                                size="16px"
                                color="white"
                                m={{ l: "0.6rem" }}
                            />
                        }
                        shadow="3"
                        hoverShadow="4"
                        bg="black800"
                        hoverBg="black500"
                        m={{ r: "0.06rem" }}
                        onClick={() => RouteChangers("Room")}
                    >
                        Login
                    </Button>
                    <Button
                        shadow="3"
                        hoverShadow="4"
                        bg="black800"
                        hoverBg="black500"
                        p={{ r: "1.5rem", l: "1rem" }}
                        maxW="95.5px"
                    >
                        Register
                    </Button>
                </div>
            </div>
        </div>
    )
}

export default Login;
